@import url(https://fonts.googleapis.com/css2?family=Itim&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(90deg, rgb(8, 133, 155), rgb(77, 74, 74));
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/*App.js*/
.App {
}
.points-container {
  position: fixed;
  color: rgb(192, 211, 229);

  text-align: center;
}
.points-container h1 {
  font-family: "Itim", cursive;
  text-shadow: 1px 0px 5px rgb(10, 10, 12);
  padding: 0px 25px 0px 10px;
  background: linear-gradient(90deg, rgb(8, 133, 155), rgba(0, 0, 0, 0.39));
  border-radius: 0px 8px 8px 0px;
}
.points-container label {
  font-size: 2rem;
  font-family: "Itim", cursive;
  text-shadow: 1px 0px 5px rgb(10, 10, 12);
}
.App-header {
  font-family: "Itim", cursive;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  color: rgb(192, 211, 229);

  text-shadow: 0px 0px 5px black;
}
body h3 {
  font-family: "Itim", cursive;
  color: rgb(255, 255, 255);
  text-shadow: 1px 0px 5px black;

  display: -webkit-flex;

  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: 2rem;
}
.popNotification {
  display: none;
}
/*toggle*/
.error {
  position: fixed;
  z-index: 5;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.63);
  font-family: "Itim", cursive;
  color: rgb(244, 196, 66);
  text-shadow: 1px 0px 5px black;
  font-size: 5rem;
  -webkit-animation: error 3s;
          animation: error 3s;
}
@-webkit-keyframes error {
  0% {
    opacity: 100%;
  }
  100% {
    opacity: 0%;
  }
}
@keyframes error {
  0% {
    opacity: 100%;
  }
  100% {
    opacity: 0%;
  }
}
/*NewQuestion.js*/
.new-question {
  font-family: "Itim", cursive;
  margin-left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  width: 80%;
  text-align: center;
  font-size: 1.8rem;
  color: rgb(192, 211, 229);
  text-shadow: 0px 0px 5px black;
  border-bottom: 1px solid black;
  padding-bottom: 15px;
}
.form {
  font-size: 2rem;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
}
.form input {
  margin-left: 5px;
  padding: 3px;
}

.form button {
  font-family: "Itim", cursive;
  font-size: 1.3rem;
  border-radius: 8px;
  display: inline;
  width: 100px;
  -webkit-align-self: center;
          align-self: center;
  margin-top: 10px;
  padding: 5px;
  color: rgb(82, 82, 82);
  font-weight: 800;
}

/*----------- GetQuestion -----------*/
.questions-container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 15px;
  gap: 15px;
  width: 80%;
  margin: auto;
}
.disply-question {
  display: -webkit-flex;
  display: flex;
  margin: 5px;
  padding: 10px;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  width: 50%;
  border-radius: 15px;
  margin-left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  background: rgba(0, 0, 0, 0.137);
}
.disply-question h1 {
  font-family: "Itim", cursive;
  color: rgb(106, 176, 179);
  text-shadow: 1px 0px 1px rgb(1, 2, 2);
  padding: 0px;
  margin: 0px;
}
.disply-question h4 {
  font-family: "Itim", cursive;
  padding: 0px;
  margin: 10px;
}
.disply-question label {
  font-family: "Itim", cursive;
  font-size: 1.3rem;
}
.disply-question input {
  padding: 3px;
  border-radius: 8px;
}
.disply-question input:focus {
  outline: none;
}

